import { Component, OnInit } from '@angular/core';
import { UserDTO } from '../models/Mesa/user.dto';
import { Router } from '@angular/router';
import { MenuController, Platform } from '@ionic/angular';
import { StorageService } from '../services/storage-service.service';
import { UserService } from '../services/user/user.service';
import { UserUpdateImgDTO } from '../models/user/user-update-img.dto';
import { MessageService } from 'primeng/api';
import { FundoService } from '../services/utils/fundo.service';

@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.page.html',
  styleUrls: ['./perfil.page.scss'],
})
export class PerfilPage implements OnInit {

  user: UserDTO;

  imgURL: string = '';

  loading = false;

  img = "";

  constructor(private router: Router, private menu: MenuController,
    public storage: StorageService, public userService: UserService,
    private toast: MessageService,
    private plataform: Platform, private fundoService: FundoService) { }

  async ngOnInit() {
    await this.plataform.ready()
      .then(() => {
        if (this.plataform.width() <= 700)
          this.fundoService.setCurrentPlataform('mobile');
        else
          this.fundoService.setCurrentPlataform('pc');

        this.img = this.fundoService.changeImg();
      });
  }

  ionViewWillEnter() {
    this.loading = true;
    let localUser = this.storage.getLocalUser();
    if (localUser && localUser.email) {
      this.userService.findByEmail(localUser.email)
        .subscribe(res => {
          this.loading = false;
          // console.log(res)
          this.user = res;
          this.imgURL = res.imgURL || 'assets/imgs/avatar-blank.png';
          //buscar imagem;
        },
          error => {
            if (error.status == 403) {
              this.loading = false;
              this.router.navigateByUrl('login');
            }
          });
    } else {
      this.loading = false;
      this.router.navigateByUrl('login');
    }
  }

  atualizar() {
    this.loading = true;

    const dto: UserUpdateImgDTO = {
      id: this.user.id,
      imgURL: this.imgURL
    }

    this.userService.updateImgUser(dto)
      .subscribe(res => {
        this.loading = false;
        this.callToast('success', 'Atualizado', 'Imagem de perfil atualizada');
      },
        error => {
          this.loading = false;
        })

  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

}
