import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { UserDTO } from 'src/app/models/Mesa/user.dto';
import { API_CONFIG } from 'src/app/config/api.config';
import { StorageService } from '../storage-service.service';
import { UserNewDTO } from 'src/app/models/user/user.new.dto';
import { UserUpdateImgDTO } from 'src/app/models/user/user-update-img.dto';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(public http: HttpClient, public storage: StorageService) { }

  findByEmail(email: string): Observable<any> {
    return this.http.get<UserDTO>(
      `${API_CONFIG.baseUrl}users/${email}`)
  }

  findById(id: string): Observable<any> {
    return this.http.get(
      `${API_CONFIG.baseUrl}users/id/${id}`)
  }

  insertNew(obj: UserNewDTO): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}users`,
      obj);
  }

  updateImgUser(dto: UserUpdateImgDTO): Observable<any> {
    return this.http.patch(`${API_CONFIG.baseUrl}users`,
      dto,
      {
        responseType: 'json',
        observe: 'response'
      })
  }
}
